import {
    REG_SUCCESS,
    REG_FAILED,
    LOGIN_SUCCESS,
    LOGIN_FAILED
} from "./types";

type Token = {
    access_token: string,
}
type PayloadType = {
    data: Token
}

type ActionsType = {
    type: string,
    payload: PayloadType,
}

const initialState = {
    code_success: false,
    access_token: null,
};

export function regReducer(state = initialState, action: ActionsType) {
    switch (action.type) {
        case REG_SUCCESS:
            return {
                ...state,
                code_success: true,
                error: null,
            };
        case REG_FAILED:
            return {
                ...state,
                code_success: false,
                error:  action.payload,
            };
        case LOGIN_SUCCESS:
            return {
                ...state,
                code_success: false,
                access_token: action.payload?.data?.access_token,
                error: null,
            };
        case LOGIN_FAILED:
            return {
                ...state,
                code_success: false,
                access_token: null,
                error:  action.payload,
            };
        default:
            return state;
    }
}

export default regReducer;
