import http from "../../../service/axios-custom";
import { API_URL } from '../../../constants';
import {
  GET_USERS_SUCCESS,
  GET_USERS_FAILED
} from "./types";

import {
  ResponseData
} from '../../../types';

export const getUsersSuccess = (data: ResponseData) => {
  return {
    type: GET_USERS_SUCCESS,
    payload: data
  }
};

export const getUsersFailed = (error: ResponseData) => {
  return {
    type: GET_USERS_FAILED,
    payload: error
  }
};

export const getUsers = (page = 1, limit = 10) => {
  return (dispatch:any) => {
    return http
      .get(`${API_URL}/users?page=${page}&limit=${limit}`)
      .then((response:any) => {
        dispatch(getUsersSuccess(response.data));
      })
      .catch((error:any) => {
        dispatch(getUsersFailed(error.response.data.message));
      });
  };
};
