import { connect } from "react-redux";
import ChatsView from "./chats.view";
import {
  getChats,
  createChat,
  selectChat,
} from "./duck/actions";

const mapDispatchToProps = (dispatch:any) => {
  return {
    selectChat: (id: number) => {
      return dispatch(selectChat(id));
    },

    getChats: (page: number, limit:number) => {
      return dispatch(getChats(page, limit));
    },

    createChat: (data: any) => {
      return dispatch(createChat(data));
    },
  };
};

const mapStateToProps = (state:any) => {
  return {
    ...state.chats,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ChatsView);
