export const login = (data: LoginData) => {
    data.phone = `+7${data.phone}`;
    return (dispatch:any) => {
        return http
          .post(`${API_URL}/authorization/login`, data)
          .then((response:any) => {
              dispatch(loginSuccess(response.data));
          })
          .catch((error:any) => {
              dispatch(loginFailed(error.response.data.message));
          });
    };
};
