import {
  GET_CHATS_SUCCESS,
  GET_CHATS_FAILED,
  GET_CHAT_SUCCESS,
  GET_CHAT_FAILED,
  CREATE_CHAT_SUCCESS,
  CREATE_CHAT_FAILED,
  SEND_MESSAGE_SUCCESS,
  SEND_MESSAGE_FAILED,
  SELECT_CHAT_SUCCESS
} from "./types";

type PayloadType = {
  data: any
}

type ActionsType = {
  type: string,
  payload: PayloadType,
}

const initialState = {
  chats: null,
  chat: null,
  message: null
};

export function chatsReducer(state = initialState, action: ActionsType) {
  switch (action.type) {
    case GET_CHATS_SUCCESS:
      return {
        ...state,
        chats: action.payload?.data,
        error: null,
      };
    case GET_CHATS_FAILED:
      return {
        ...state,
        chats: null,
        error:  action.payload?.data,
      };
    case GET_CHAT_SUCCESS:
      return {
        ...state,
        chat: action.payload?.data,
        error: null,
      };
    case GET_CHAT_FAILED:
      return {
        ...state,
        chat: null,
        error:  action.payload?.data,
      };
    case CREATE_CHAT_SUCCESS:
      return {
        ...state,
        chat: action.payload?.data,
        error: null,
      };
    case CREATE_CHAT_FAILED:
      return {
        ...state,
        chat: null,
        error:  action.payload?.data,
      };
    case SEND_MESSAGE_SUCCESS:
      return  {
        ...state,
        message: action.payload?.data,
        error: null,
      }
    case SEND_MESSAGE_FAILED:
      return  {
        ...state,
        message: null,
        error: action.payload?.data,
      }
    case SELECT_CHAT_SUCCESS:
      return  {
        ...state,
        chatId: action.payload?.data?.chatId,
        error: action.payload?.data,
      }

    default:
      return state;
  }
}

export default chatsReducer;
