import {
  GET_USERS_SUCCESS,
  GET_USERS_FAILED
} from "./types";

type PayloadType = {
  data: any
}

type ActionsType = {
  type: string,
  payload: PayloadType,
}

const initialState = {
  chats: null,
  chat: null
};

export function contactsReducer(state = initialState, action: ActionsType) {
  switch (action.type) {
    case GET_USERS_SUCCESS:
      return {
        ...state,
        users: action.payload?.data,
        error: null,
      };
    case GET_USERS_FAILED:
      return {
        ...state,
        users: null,
        error:  action.payload?.data,
      };
    default:
      return state;
  }
}

export default contactsReducer;
