import {
  GET_PROFILE_SUCCESS,
  GET_PROFILE_FAILED,
  UPDATE_PROFILE_SUCCESS,
  UPDATE_PROFILE_FAILED,
} from "./types";

type Token = {
  access_token: string,
}
type PayloadType = {
  data: Token
}

type ActionsType = {
  type: string,
  payload: PayloadType,
}

const initialState = {
  profile: null
};

export function profileReducer(state = initialState, action: ActionsType) {
  switch (action.type) {
    case GET_PROFILE_SUCCESS:
      return {
        ...state,
        profile: action.payload?.data,
        error: null,
      };
    case GET_PROFILE_FAILED:
      return {
        ...state,
        profile: null,
        error:  action.payload?.data,
      };
    case UPDATE_PROFILE_SUCCESS:
      return {
        ...state,
        profile: action.payload?.data,
        error: null,
      };
    case UPDATE_PROFILE_FAILED:
      return {
        ...state,
        profile: null,
        error:  action.payload?.data,
      };
    default:
      return state;
  }
}

export default profileReducer;
