import React, { useEffect, useState, useRef } from 'react';
import { SOCKET_URL } from '../../../constants';
import { io } from "socket.io-client";
import moment from 'moment';
import { useWindowSize } from "../../../service";
import { Input, Button } from 'antd';
import { SendOutlined } from '@ant-design/icons';
import { MessageData } from '../../../types';

import { _isAuthenticated } from '../../../service';

import './chat.less';

const socket = io(SOCKET_URL, {
  extraHeaders: {
    Authorization: `Bearer ${_isAuthenticated()}`
  }
});

function ChatView(props: any) {
  const [width, height] = useWindowSize();
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([]);
  const [isConnected, setIsConnected] = useState(socket.connected);
  const bottomRef = useRef(null);
  const [currentChat, setCurrentChat] = useState(null);
  const [isFetched, setIsFetched] = useState(false);

  const handleEmitMessage = (message: any) => {
    console.log(currentChat);
    if (currentChat || props.chatId) {
      props.getChat(currentChat || props.chatId);
      setIsFetched(true);
    }
    setTimeout(() => {
      // @ts-ignore
      //messagesBlock.current.scrollTop = messagesBlock.current.scrollHeight
    }, 100);
    //messagesBlock.current.scrollTop = messagesBlock.current.scrollHeight + 500;
  }

  useEffect(() => {
    socket.on('connect', () => {
      setIsConnected(true);
    });

    socket.on('disconnect', () => {
      setIsConnected(false);
      console.log('disconnect')
    });

    return () => {
      socket.off('connect');
      socket.off('disconnect');
    };
  }, []);

  useEffect(() => {
    if (props.chatId) {
      setCurrentChat(props.chatId);
      props.getChat(Number(props.chatId));

      socket.on('receiveMessage', (data) => {
        handleEmitMessage(data.message);
      });
    }

    return () => {
      socket.off('connect');
      socket.off('disconnect');
    };
  }, [props.chatId]);

  useEffect(() => {
    if (props?.chat) {
      setMessages(props.chat.message.reverse());
      // @ts-ignore
      //messagesBlock.current.scrollTop = messagesBlock.current.scrollHeight;
    }
  }, [props.chat]);

  useEffect(() => {
    // @ts-ignore
    bottomRef.current?.scrollIntoView({behavior: 'smooth'});
  }, [messages])

  const handleSendMessage = () => {
    if (message && currentChat) {
      props.sendMessage(currentChat, {
        message_type: 'text',
        text: message,
      });
      setMessage('');
    }
  };

  return(
    <>
      <div className="chat">
        <div
          style={{
            height: `${height - 80}px`,
            overflowY: 'scroll'
          }}
        >
          <ul
            className="chat-messages"
          >
            {
              messages?.map((message: MessageData) =>
                <li className={`message-${props.userId === message.initiator_id ? 'self' : 'internal'}`}>
                  <span>{message.text}</span>
                  <span className="message-date">
                  {moment(message.created_at).format('HH:mm')}
                </span>
                </li>
              )
            }
            <div ref={bottomRef} />
          </ul>
        </div>
        <Input.Group
          style={{
            display: 'flex',
            alignItems: 'center'
          }}
        >
          <Input
            value={message}
            onInput={(event:any) => {
              setMessage(event.target.value)
            }}
            placeholder=" "
            style={{
              width: 'calc(100% - 40px)',
              background: '#2E283B',
              border: 'none',
              color: '#fff',
              outline: 'none'
            }}
          />
          <Button
            onClick={handleSendMessage}
            style={{
              background: '#2E283B',
              border: 'none',
              padding: 0,
              borderBottomLeftRadius: 0,
              borderTopLeftRadius: 0,
              height: '38px'
            }}
            icon={
              <SendOutlined
                style={{
                  color: '#076ED1'
                }}
            />}
          />
        </Input.Group>
      </div>
    </>
  );
}

export default ChatView;
