import React, { useEffect } from "react";
import SvgIcon from "../../components/svg";
import { RegData } from "../../types";
import { Form, Button, Input } from "antd";
import "./registration.less";

function RegistrationView(props: any) {
    useEffect(() => {
        if (props.access_token) {
            props.saveToken(props.access_token);
            window.location.href = '/profile';
        }
    }, [props]);

    const onFinish = (values: RegData) => {
        if (props?.code_success) {
            console.log(1)
            props.login(values);
        } else {
            props.sendCode(values?.phone);
        }
    };

    return(
        <>
            <div className="reg-form">
                <SvgIcon name="logo" />
                <Form
                    layout="vertical"
                    className="login-form"
                    onFinish={onFinish}
                >
                    <div className="form-items-wrapper">
                        <Form.Item
                            name="phone"
                            label=" "
                            rules={[{ required: true, message: "    "}]}
                        >
                            <Input
                              prefix="+7"
                              size="large"
                            />
                        </Form.Item>
                        {
                            props?.code_success &&
                            <Form.Item
                              name="code"
                              label=""
                              rules={[{ required: true, message: "    "}]}
                            >
                                <Input
                                  size="large"
                                />
                            </Form.Item>
                        }
                        {props.error && <p className="error">{props.error}</p>}
                    </div>
                    <Button
                        type="primary"
                        htmlType="submit"
                        size="large"
                    >
                        {props?.code_success ? '' : ' '}
                    </Button>
                </Form>
            </div>
        </>
    );
}

export default RegistrationView;
