import Cookies from 'universal-cookie';
import { useLayoutEffect, useState } from "react";

const cookies = new Cookies();

export const saveTokenToCookie = (token: string) => {
    cookies.set('access_token', token, {path: '/', domain: window.location.hostname});
};

export const _isAuthenticated = () => {
    return cookies.get('access_token');
}

export const signOut = () => {
    cookies.remove('access_token', {path: '/', domain: window.location.hostname});
    window.location.reload();
};

export function useWindowSize() {
    const [size, setSize] = useState([0, 0]);
    useLayoutEffect(() => {
        function updateSize() {
            setSize([window.innerWidth, window.innerHeight]);
        }
        window.addEventListener('resize', updateSize);
        updateSize();
        return () => window.removeEventListener('resize', updateSize);
    }, []);
    return size;
}
