import http from "../../../service/axios-custom";
import { API_URL } from '../../../constants';
import {
  GET_CHATS_SUCCESS,
  GET_CHATS_FAILED,
  GET_CHAT_SUCCESS,
  GET_CHAT_FAILED,
  CREATE_CHAT_SUCCESS,
  CREATE_CHAT_FAILED,
  SEND_MESSAGE_SUCCESS,
  SEND_MESSAGE_FAILED,
  SELECT_CHAT_SUCCESS,
} from "./types";

import {
  ChatData,
  MessageData,
  ResponseData,
} from '../../../types';


export const selectChat = (id: number) => {
  return {
    type: SELECT_CHAT_SUCCESS,
    payload: {
      data: {
        chatId: id
      }
    }
  }
};

export const getChatsSuccess = (data: ResponseData) => {
  return {
    type: GET_CHATS_SUCCESS,
    payload: data
  }
};

export const getChatsFailed = (error: ResponseData) => {
  return {
    type: GET_CHATS_FAILED,
    payload: error
  }
};

export const getChatSuccess = (data: ResponseData) => {
  return {
    type: GET_CHAT_SUCCESS,
    payload: data
  }
};

export const getChatFailed = (error: ResponseData) => {
  return {
    type: GET_CHAT_FAILED,
    payload: error
  }
};

export const createChatSuccess = (data: ResponseData) => {
  return {
    type: CREATE_CHAT_SUCCESS,
    payload: data
  }
};

export const createChatFailed = (error: ResponseData) => {
  return {
    type: CREATE_CHAT_FAILED,
    payload: error
  }
};

export const sendMessageSuccess = (data: ResponseData) => {
  return {
    type: SEND_MESSAGE_SUCCESS,
    payload: data
  }
};

export const sendMessageFailed = (error: ResponseData) => {
  return {
    type: SEND_MESSAGE_FAILED,
    payload: error
  }
};

export const getChats = (page = 1, limit = 10) => {
  return (dispatch:any) => {
    return http
      .get(`${API_URL}/chats?page=${page}&limit=${limit}`)
      .then((response:any) => {
        dispatch(getChatsSuccess(response.data));
      })
      .catch((error:any) => {
        dispatch(getChatsFailed(error.response.data.message));
      });
  };
};

export const createChat = (data: ChatData) => {
  return (dispatch:any) => {
    return http
      .post(`${API_URL}/chats`, data)
      .then((response:any) => {
        dispatch(createChatSuccess(response.data));
      })
      .catch((error:any) => {
        dispatch(createChatFailed(error.response.data.message));
      });
  };
};

export const getChat = (chat_id: number) => {
  return (dispatch:any) => {
    return http
      .get(`${API_URL}/chats/${chat_id}`)
      .then((response:any) => {
        dispatch(getChatSuccess(response.data));
      })
      .catch((error:any) => {
        dispatch(getChatFailed(error.response.data.message));
      });
  };
};

export const sendMessage = (chat_id: number, message: MessageData) => {
  return (dispatch:any) => {
    return http
      .post(`${API_URL}/chats/message/${chat_id}`, message)
      .then((response:any) => {
        dispatch(sendMessageSuccess(response.data));
      })
      .catch((error:any) => {
        dispatch(sendMessageFailed(error.response.data.message));
      });
  };
};
