import http from "../../../service/axios-custom";
import { API_URL } from '../../../constants';
import {
  GET_PROFILE_SUCCESS,
  GET_PROFILE_FAILED,
  UPDATE_PROFILE_SUCCESS,
  UPDATE_PROFILE_FAILED,
} from "./types";

import {
  ProfileData,
  ResponseData
} from '../../../types';

export const getProfileSuccess = (data: ResponseData) => {
  return {
    type: GET_PROFILE_SUCCESS,
    payload: data
  }
};

export const getProfileFailed = (error: ResponseData) => {
  return {
    type: GET_PROFILE_FAILED,
    payload: error
  }
};

export const updateProfileSuccess = (data: ResponseData) => {
  return {
    type: UPDATE_PROFILE_SUCCESS,
    payload: data
  }
};

export const updateProfileFailed = (error: ResponseData) => {
  return {
    type: UPDATE_PROFILE_FAILED,
    payload: error
  }
};

export const getProfile = () => {
  return (dispatch:any) => {
    return http
      .get(`${API_URL}/profile`)
      .then((response:any) => {
        dispatch(getProfileSuccess(response.data));
      })
      .catch((error:any) => {
        dispatch(getProfileFailed(error.response.data.message));
      });
  };
};

export const updateProfile = (data: ProfileData) => {
  return (dispatch:any) => {
    return http
      .patch(`${API_URL}/profile`, data)
      .then((response:any) => {
        dispatch(updateProfileSuccess(response.data));
      })
      .catch((error:any) => {
        dispatch(updateProfileFailed(error.response.data.message));
      });
  };
};
