import { connect } from "react-redux";
import jwt_decode from 'jwt-decode';
import ChatView from "./chat.view";
import {
  getChat,
  sendMessage
} from "../duck/actions";
import { _isAuthenticated } from '../../../service';
import { MessageData } from '../../../types';

const mapDispatchToProps = (dispatch:any) => {
  return {
    getChat: (id: number) => {
      return dispatch(getChat(id));
    },

    sendMessage: (chat_id: number, data: MessageData) => {
      return dispatch(sendMessage(chat_id, data));
    },
  };
};

const mapStateToProps = (state:any) => {
  const token = _isAuthenticated();
  const decoded:{id: number} = jwt_decode(token);
  return {
    ...state.chats,
    userId: decoded.id,
    token: token,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ChatView);
